/*------------------------------------*  #GENERIC-RESET
\*------------------------------------*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  font-family: 'Roboto', Helvetica, Arial, sans-serif;
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline; }

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

button,
input [type="submit"] {
  background-color: none; }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: border-box; }

/*------------------------------------*  #BASE-HEADERS
\*------------------------------------*/
h1 {
  font-size: 36px; }

h2 {
  font-size: 28px; }

h3 {
  font-size: 24px; }

h4 {
  font-size: 20px; }

h5 {
  font-size: 18px; }

h6 {
  font-size: 16px; }

a {
  text-decoration: none; }
  a:hover {
    cursor: pointer; }

/*------------------------------------*  #SETTINGS-GLOBALS
\*------------------------------------*/
/*------------------------------------*  #SETTINGS-GLOBALS
\*------------------------------------*/
/*------------------------------------*  #SETTINGS-Spacing
\*------------------------------------*/
/*------------------------------------*  #COMPONENT-HEADINGS
\*------------------------------------*/
/*------------------------------------*  #SETTINGS-GLOBALS
\*------------------------------------*/
/*------------------------------------*  #SETTINGS-GLOBALS
\*------------------------------------*/
/*------------------------------------*  #SETTINGS-Spacing
\*------------------------------------*/
.title, .title--page, .title--sect, .title--sect__disclaim, .title--scene {
  margin: 0 0 1rem 0;
  text-align: center;
  color: #495867; }
  .title--page {
    text-align: center;
    color: #495867; }
  .title--sect {
    color: #577399;
    text-align: left; }
    .title--sect__disclaim {
      color: #fc3a3a;
      text-align: center; }
  .title--scene {
    color: #577399;
    text-align: center; }

.navbar {
  overflow: hidden; }

/*------------------------------------*  #COMPONENT-NAV
\*------------------------------------*/
.navbar {
  overflow: hidden; }

.nav {
  text-align: center; }
  .nav--item {
    display: inline-block;
    padding: .5rem .5rem .5rem .25rem;
    vertical-align: middle; }
  .nav--link {
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1); }
    .nav--link:hover {
      color: green; }

/*------------------------------------*  #COMPONENT-BTN
\*------------------------------------*/
/*------------------------------------*  #SETTINGS-GLOBALS
\*------------------------------------*/
/*------------------------------------*  #SETTINGS-GLOBALS
\*------------------------------------*/
/*------------------------------------*  #SETTINGS-Spacing
\*------------------------------------*/
.btn, .btn--primary, .btn--secondary, .btn--promo {
  display: inline-block;
  text-decoration: none;
  padding: 7px 20px;
  border-radius: 3px;
  cursor: pointer;
  border: 1px solid #495867;
  color: #495867;
  background-color: #f7f7ff;
  margin: 2px;
  text-align: center;
  font-size: 16px;
  text-overflow: none; }
  .btn--primary {
    background-color: #fc3a3a;
    border-color: #fc3a3a;
    color: #f7f7ff; }
  .btn--secondary {
    background-color: #f7f7ff;
    border-color: #fc3a3a;
    color: #fc3a3a; }
  .btn--promo {
    background-color: #577399;
    border-color: #577399;
    color: #f7f7ff; }

.btn--med {
  padding: 7px 40px; }

.btn--lg {
  padding: 10px 80px; }

/*------------------------------------*  #LAYOUT-ALL
\*------------------------------------*/
/*------------------------------------*  #LAYOUT-CONTAINERS
\*------------------------------------*/
.container {
  padding: 1rem 1rem 1rem 1rem; }
